/*============    	АДАПТИВ    ===========*/
.dropdown-toggle{
	border: 1px solid #000;
	padding: 10px 10px;
	border-radius:5px;
	&:hover{
		background-color:#1AB798;
		color:#fff;
	}
	i{
		margin-top: 15px;
		font-size:16px;
	}
}
@media(min-width:0px) and (max-width:320px) {
	nav#down_menu{
		display: none;
	}
	nav.navbar-fixed-top {
    	margin-top:10px;
    	.container{
	        h1{
		        font-size:20px;
		        margin:12px;
		    }
		    ul.dropdown-menu{
		    	margin-top:25px;
		        li{
		            padding:10px;
		        }
		    }
		}
	    &.sticky{
	        margin-top:0px;
	        position:fixed;
	        height: 0px;
	        .container{
	            -webkit-box-shadow: 1px 9px 15px -9px rgba(0,0,0,0.63);
	            -moz-box-shadow: 1px 9px 15px -9px rgba(0,0,0,0.63);
	            box-shadow: 1px 9px 15px -9px rgba(0,0,0,0.63);
	        }
	    }
    }

	section#First_screen{
		h1{
			font-size:10px;
		}
	}
}
